.loader {
  margin-top: 20%;
  margin-left: 30%;
}

.lds-dual-ring {
  display: inline-block;
  width: 64px;
  height: 64px;
}
.lds-dual-ring:after {
  content: ' ';
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #000;
  border-color: #000 transparent #000 transparent;
  -webkit-animation: lds-dual-ring 1.2s linear infinite;
          animation: lds-dual-ring 1.2s linear infinite;
}
@-webkit-keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.uniform-control__vec2 input {
  width: 8em;
}

.uniform-control__slider {
  padding-bottom: 0.5em;
  border-bottom: 1px solid #ddd;
}

.uniform-control__color {
  padding: 0.5em 0em;
}
.uniform-control__color strong {
  padding-right: 1em;
}

.uniform-control__checkbox {
  padding: 0.5em 0em;
}
.uniform-control__checkbox strong {
  padding-right: 1em;
}

.uniform-control {
