}

.react-precision-slider {
  text-align: left;
}

.rpc-reset {
  cursor: pointer;
}

.rpc-value {
  float: right;
}

.rpc-value-bar {
  width: 100%;
  height: 5px;
  background: -webkit-repeating-linear-gradient(
    left,
    #f6ba52,
    #f6ba52 10px,
    #ffd180 10px,
    #ffd180 20px
  );
  background: repeating-linear-gradient(
    to right,
    #f6ba52,
    #f6ba52 10px,
    #ffd180 10px,
    #ffd180 20px
  );
}

